div.seperator-wrapper {
  width: 100%;
  min-height: 450px;
  border-top: 6px solid $navy;
  border-bottom: 6px solid $navy;
  /* Create the parallax scrolling effect */
  background-image: url('https://photos.smugmug.com/Beautiful-Cities-on-Planet-Earth/i-JrMZRkZ/0/667d2ae0/4K/Trey%20Ratcliff%20-%20Cityscapes%20%20-%202-4K.jpg');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}
